"use client";

import { useState, useEffect, useMemo, useCallback } from 'react';
import { type PaginatedResponse } from '../services/api/types';
import { isToday, isThisWeek, isThisMonth } from '../utils/dateUtils';

/**
 * ورودی‌های هوک useListPage.
 * T نوع داده‌ای آیتم‌های لیست است (مثلاً Shop).
 */
export interface UseListPageOptions<T> {
  /**
   * تابعی که داده‌ها را از API واکشی می‌کند.
   */
  fetchData: (page: number, pageSize: number, filters?: any) => Promise<PaginatedResponse<T>>;
  /**
   * کلیدهایی از آبجکت T که در آن‌ها جستجوی متنی انجام می‌شود.
   */
  searchKeys: (keyof T)[];
  /**
   * کلیدی از آبجکت T که تاریخ آیتم را برای فیلتر زمانی مشخص می‌کند.
   */
  dateKey: keyof T;
  /**
   * کلیدی از آبجکت T که وضعیت آیتم را برای فیلتر وضعیت مشخص می‌کند.
   */
  statusKey: keyof T;
}

/**
 * یک هوک سفارشی که تمام منطق یک صفحه لیست (واکشی، فیلتر، جستجو، پجینیشن) را مدیریت می‌کند.
 */
export const useListPage = <T extends {}>({
  fetchData,
  searchKeys,
  dateKey,
  statusKey,
}: UseListPageOptions<T>) => {
  // --- State Management ---
  const [allData, setAllData] = useState<T[]>([]);
  const [totalServerCount, setTotalServerCount] = useState(0);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [pageSize, setPageSize] = useState(10);
  const [currentPage, setCurrentPage] = useState(1);
  const [searchTerm, setSearchTerm] = useState('');
  const [activeTimeFilter, setActiveTimeFilter] = useState('newest');
  const [selectedStatus, setSelectedStatus] = useState('all');

  // --- Data Fetching ---
  // تابع واکشی را با useCallback پایدار می‌کنیم تا بتوانیم آن را به عنوان وابستگی useEffect استفاده کنیم
  const loadData = useCallback(async () => {
    try {
      setIsLoading(true);
      setError(null);
      // در آینده، می‌توانیم فیلترهای searchTerm و selectedStatus را هم به API پاس دهیم
      const response = await fetchData(currentPage, pageSize);
      // اگر فیلتر سمت کلاینت است، allData را فقط یک بار آپدیت می‌کنیم
      if (currentPage === 1 && searchTerm === '' && selectedStatus === 'all' && activeTimeFilter === 'newest') {
        setAllData(response.items);
      }
      setTotalServerCount(response.totalCount); // تعداد کل از سرور
    } catch (err) {
      setError(err instanceof Error ? err.message : "خطا در واکشی داده‌ها");
    } finally {
      setIsLoading(false);
    }
  }, [fetchData, currentPage, pageSize, searchTerm, selectedStatus, activeTimeFilter]); // به تمام فیلترها وابسته است

  useEffect(() => {
    loadData();
  }, [loadData]); // useEffect حالا به تابع پایدار loadData وابسته است

  // --- Client-Side Filtering & Sorting ---
  const filteredAndSortedData = useMemo(() => {
    let filtered = [...allData];

    // مرحله ۱: فیلتر بر اساس جستجو
    if (searchTerm) {
      const lowercasedFilter = searchTerm.toLowerCase();
      filtered = filtered.filter(item =>
        searchKeys.some(key => 
          String(item[key]).toLowerCase().includes(lowercasedFilter)
        )
      );
    }
    
    // مرحله ۲: فیلتر بر اساس وضعیت
    if (selectedStatus !== 'all') {
      // مقدار selectedStatus ممکن است boolean باشد (true/false)
      // پس آن را با مقدار فیلد مقایسه می‌کنیم
      const statusValue = selectedStatus === 'true' ? true : selectedStatus === 'false' ? false : selectedStatus;
      filtered = filtered.filter(item => item[statusKey] === statusValue);
    }
    
    // مرحله ۳: فیلتر و مرتب‌سازی بر اساس زمان
    switch (activeTimeFilter) {
      case 'today':
        filtered = filtered.filter(item => isToday(String(item[dateKey])));
        break;
      case 'this_week':
        filtered = filtered.filter(item => isThisWeek(String(item[dateKey])));
        break;
      case 'this_month':
        filtered = filtered.filter(item => isThisMonth(String(item[dateKey])));
        break;
      case 'oldest':
        filtered.sort((a, b) => new Date(String(a[dateKey])).getTime() - new Date(String(b[dateKey])).getTime());
        break;
      case 'newest':
      default:
        filtered.sort((a, b) => new Date(String(b[dateKey])).getTime() - new Date(String(a[dateKey])).getTime());
        break;
    }
    
    return filtered;
  }, [allData, searchTerm, activeTimeFilter, selectedStatus, searchKeys, dateKey, statusKey]);

  // --- Pagination on Filtered Data ---
  const paginatedData = useMemo(() => {
    const startIndex = (currentPage - 1) * pageSize;
    return filteredAndSortedData.slice(startIndex, startIndex + pageSize);
  }, [filteredAndSortedData, currentPage, pageSize]);

  // --- Handlers ---
  const handlePageChange = (page: number) => setCurrentPage(page);
  const handlePageSizeChange = (size: number) => { setPageSize(size); setCurrentPage(1); };
  const handleSearchChange = (event: React.ChangeEvent<HTMLInputElement>) => { setSearchTerm(event.target.value); setCurrentPage(1); };
  const handleTimeFilterChange = (tabId: string) => { setActiveTimeFilter(tabId); setCurrentPage(1); };
  const handleStatusChange = (event: React.ChangeEvent<HTMLSelectElement>) => { setSelectedStatus(event.target.value); setCurrentPage(1); };
  
  // --- Hook's Public API ---
  return {
    data: paginatedData,
    totalCount: filteredAndSortedData.length,
    isLoading,
    error,
    currentPage,
    pageSize,
    searchTerm,
    activeTimeFilter,
    selectedStatus,
    handlePageChange,
    handlePageSizeChange,
    handleSearchChange,
    handleTimeFilterChange,
    handleStatusChange,
    refreshData: loadData, // تابع برای رفرش دستی لیست
  };
};